import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Rx';

export interface ContactSubmission {
  name: string;
  email: string;
  phone: string;
  message: string;
  _gotcha: string;
}

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ContactComponent implements OnInit {

  contact: FormGroup;

  constructor(public http: Http, public router: Router) { }

  ngOnInit() {
    this.contact = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.minLength(2)]),
      email: new FormControl('', Validators.required),
      phone: new FormControl('', [Validators.required, Validators.minLength(2)]),
      message: new FormControl(''),
      _gotcha: new FormControl()
    });
  }

  sendForm({ value, valid }: { value: ContactSubmission, valid: boolean }): void {
    console.log('sendForm hit. the value that will be sent is: ', value);
    if (valid) {
      let headers = new Headers({ 'Accept': 'application/json', 'Content-Type': 'application/json' });
      let options = new RequestOptions({ headers: headers });

      let url = `https://formspree.io/buildwelshhills@gmail.com`;

      this.http.post(url, value, options)
        .map((res: Response) => {
          let response = res.json();
          let data = response.data || {};
          console.log(`
            SUCCESS! The value sent was: ${value} 
            
            The response was: ${res}
          `);
          this.router.navigateByUrl('/thanks');
        })
        .catch((error: Response | any) => {
          // In a real world app, we might use a remote logging infrastructure
          let errMsg: string;
          if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
          } else {
            errMsg = error.message ? error.message : error.toString();
          }
          console.error(errMsg);
          return Observable.throw(errMsg);
        });
    }

  }

}



